.forfait-card {
  padding: 20px;
  border-radius: 10px;
  background: #e0e0e0;

  .flex-between {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .title {
    font-weight: bold;
    margin: 0;
  }

  .info-block {
    margin-top: 10px;
    .info-item {
      margin-top: 3px;
      &:last-child {
        margin-bottom: 0;
      }
    }

    .description {
      font-size: 0.8em;
      color: #666;
    }
  }

  .link {
    display: inline-block;
    margin-top: 10px;
    cursor: pointer;
    text-decoration: underline;
    color: #0000ee;
  }
}

.cgu-link {
  text-decoration: none;
  color: #0000ee;
  cursor: pointer;
}

.error-container {
  text-align: center;
  padding: 20px;

  .retry-button {
    margin-top: 16px;
  }
}
.payment-notice {
  border: 1px solid var(--ion-color-primary);
  border-radius: 8px;
  margin-bottom: 12px;
  background-color: var(--ion-color-primary);

  p {
    font-size: 12px;
    color: white;
    margin: 8px;
    line-height: 1.4;
  }
}

ion-item.card-item {
  border-radius: 8px;
  border: 1px solid gray;
}
.divider {
  border-top: 1px solid #ccc;
  margin: 16px 0;
}
#payment-element {
  padding: 16px;
  margin: 16px 0;
}

.google-pay-button {
  span {
    font-size: 20px;
  }
}

.apple-pay-button {
  span {
    font-size: 20px;
  }
}
